define(function () {
    "use strict";

    var cookies = require('cookies');

    window.NinjaMode = (cookies.getCookie('NinjaMode') === 'true');

    String.prototype.format = String.prototype.f = function () {
        var s = this,
            i = arguments.length;

        while (i--) {
            s = s.replace(new RegExp('\\{' + i + '\\}', 'gm'), arguments[i]);
        }
        return s;
    };

    var docs = {
        getMetadata: function (name) {
            if (!name) {
                return;
            }
            return $("meta[name='{0}']".format(name)).attr("content");
        },

        getPageRelativePath: function (target) {
            var toc = this.getMetadata("toc_rel");
            var currentFolder = toc.toLowerCase().split('toc.json')[0];
            return currentFolder + target;
        },

        toAbsolutePath: function (relativePath) {
            var currentPath = location.pathname.substr(0, location.pathname.lastIndexOf('/'));
            if (relativePath.indexOf('/') === 0) {
                return relativePath;
            } else if (relativePath.indexOf('..') !== 0) {
                return currentPath + '/' + relativePath;
            } else {
                relativePath.split('/').forEach(function (item) {
                    if (item === '..') {
                        currentPath = currentPath.substr(0, currentPath.lastIndexOf('/'));
                    }
                    else {
                        currentPath += '/' + item;
                    }
                });
                return currentPath;
            }
        },

        getJsonFile: function (jsonURL) {
            if (!jsonURL) {
                return;
            }
            // TODO: Introduce Promise and make the ajax call async.
            var json = (function () {
                var json = null;
                $.ajax({
                    'async': false,
                    'global': false,
                    'url': jsonURL,
                    'dataType': "json",
                    'success': function (data) {
                        json = data;
                    }
                });
                return json;
            })();
            return json;
        },

        filterContent: function (platform, language) {
            // filter platform
            $('.list-clean > .platform').each(function () {
                $(this).attr('hidden', true);
            });
            if (platform) {
                $('li.' + platform).attr('hidden', false);
            }
            $('header#overview span').each(function(){
                $(this).attr('hidden', true);
            });

            // display None content to remain the area
            $('.inheritance code').each(function () {
                if ($(this).text() !== 'None') {
                    $(this).attr('hidden', true);
                }
            });
            $('code[lang]').attr('hidden', true);
            $('code[class]').attr('hidden', true);

            $('.' + language).attr('hidden', false);
            $('code[lang=\'' + language + '\']').each(function () {
                $(this).attr('hidden', false);
            });

            var navigation = this.buildNav('navi');
            var mobilenavi = this.buildNav('mobilenavi');
            $('.navigation').empty();
            $('.navigation').append(navigation);
            $('.mobilenavi > select').empty();
            $('.mobilenavi > select').append(mobilenavi);
        },

        // build navigation
        buildNav: function (type) {
            var firstNav, secondNav;
            if (type === 'mobilenavi') {
                firstNav = '<option value="{0}">{1}</option>';
                secondNav = '<option value="{0}">&nbsp;&nbsp;{1}</option>';
            }
            else if (type === 'navi') {
                firstNav = '<h2><a href="#{0}">{1}</a></h2>';
                secondNav = '<li><a href="#{0}" title="">{1}</a></li>';
            }
            var navigation = firstNav.format('overview', 'Overview');
            var overviewFlag = false;
            if ($('header#summary').length) {
                overviewFlag = true;
                navigation += '<ul>' + secondNav.format('summary', 'Introduction');
            }
            if ($('header#conceptual').length) {
                overviewFlag = true;
                navigation += secondNav.format('conceptual', 'Using');
            }
            if ($('header#remarks').length) {
                overviewFlag = true;
                navigation += secondNav.format('remarks', 'Remarks');
            }
            if (overviewFlag) {
                navigation += '</ul>';
            }
            else {
                navigation = '';
            }
            // class block
            if ($('header#constructors').length) {
                navigation = this.buildCodeNav(navigation, 'constructors', firstNav, secondNav);
            }
            if ($('header#fields').length) {
                navigation = this.buildCodeNav(navigation, 'fields', firstNav, secondNav);
            }
            if ($('header#properties').length) {
                navigation = this.buildCodeNav(navigation, 'properties', firstNav, secondNav);
            }
            if ($('header#methods').length) {
                navigation = this.buildCodeNav(navigation, 'methods', firstNav, secondNav);
            }
            if ($('header#events').length) {
                navigation = this.buildCodeNav(navigation, 'events', firstNav, secondNav);
            }
            if ($('header#operators').length) {
                navigation = this.buildCodeNav(navigation, 'operators', firstNav, secondNav);
            }
            // namespace block
            if ($('header#classes').length) {
                navigation = this.buildCodeNav(navigation, 'classes', firstNav, secondNav);
            }
            if ($('header#interfaces').length) {
                navigation = this.buildCodeNav(navigation, 'interfaces', firstNav, secondNav);
            }
            if ($('header#structs').length) {
                navigation = this.buildCodeNav(navigation, 'structs', firstNav, secondNav);
            }
            if ($('header#enums').length) {
                navigation = this.buildCodeNav(navigation, 'enums', firstNav, secondNav);
            }
            if ($('header#delegates').length) {
                navigation = this.buildCodeNav(navigation, 'delegates', firstNav, secondNav);
            }
            return navigation;
        },

        buildCodeNav: function (navigation, header, firstNav, secondNav) {
            var parentNav = firstNav.format(header, header) + '<ul>';
            var childNav = '';
            $('header#' + header).next().children().filter(':visible').each(function () {
                var id = $(this).attr('id');
                var name = $(this).find('h4 code:visible').text();
                childNav += secondNav.format(id, name);
            });
            if (childNav.length) {
                $('header#' + header).attr('hidden', false);
                navigation += parentNav + childNav + '</ul>';
            }
            else {
                $('header#' + header).attr('hidden', true);
            }
            return navigation;
        }
    };

    docs.breadcrumb = {
        buildReferenceBreadcrumb: function () {
            var breadcrumb = '';
            var namespace = '';

            var bcTemplate = '<li><a href="{0}">{1}</a></li>';
            var loc = docs.getMetadata('toc_rel');
            var title = document.title.split(' ')[0];
            var json = docs.getJsonFile(loc);
            json.forEach(function (item) {
                // namespace detected
                if (item.toc_title === title) {
                    breadcrumb += '<li>' + document.title + '</li>';
                    namespace = item.toc_title;
                    return breadcrumb;
                }
                else if (item.children !== undefined) {
                    item.children.forEach(function (child) {
                        // class detected
                        if (child.toc_title === title) {
                            var href = '';
                            if (item.href !== undefined) {
                                href = docs.getPageRelativePath(item.href) + '.html';
                            }
                            breadcrumb += bcTemplate.format(href, item.toc_title + ' namespace');
                            namespace = item.toc_title;
                            breadcrumb += '<li>' + document.title + '</li>';
                        }
                        return breadcrumb;
                    });
                }
            });
            var globalBC = this.buildGlobalBreadcrumb(namespace, 'reference');
            breadcrumb = globalBC.substr(0, globalBC.lastIndexOf('<li>')) + breadcrumb;
            return breadcrumb;
        },

        buildGlobalBreadcrumb: function (target, type) {
            var breadcrumb = '';
            var bcTemplate = '<li><a href="{0}">{1}</a></li>';
            var loc = docs.getMetadata('breadcrumb_path');
            if (type === 'conceptual') {
                var tocloc = docs.getMetadata('toc_rel');
                target = docs.toAbsolutePath(tocloc).toLowerCase().split('toc.json')[0];
            }
            var json = docs.getJsonFile(loc);
            if (!json) {
                return breadcrumb;
            }
            if (type === 'reference') {
                json.forEach(function (item) {
                    breadcrumb = this.findTargetNamespace(item, target, breadcrumb);
                }, this);
            }
            else {
                json.forEach(function (item) {
                    breadcrumb = this.findTargetLoc(item, docs.toAbsolutePath(tocloc).toLowerCase().split('toc.json')[0], breadcrumb);
                }, this);
            }
            return breadcrumb;
        },

        findTargetLoc: function (toc, targetTOC, path) {
            targetTOC = targetTOC.toLowerCase();
            if (toc.relative_path_in_depot && toc.relative_path_in_depot.toLowerCase() === targetTOC) {
                path = '<li>' + toc.toc_title + '</li>';
                return path;
            }
            else if (toc.children && path.length === 0) {
                toc.children.forEach(function (item) {
                    path = this.findTargetLoc(item, targetTOC, path);
                    return path;
                }, this);
                if (path.length) {
                    var href = '';
                    if (toc.homepage) {
                        href = toc.homepage;
                    }
                    path = '<li><a href="' + href + '">' + toc.toc_title + '</a></li>' + path;
                    return path;
                }
            }
            return path;
        },

        findTargetNamespace: function (toc, namespace, path) {
            namespace = namespace.toLowerCase();
            if (toc.toc_title && toc.toc_title.toLowerCase() === namespace) {
                path = '<li>' + toc.toc_title + '</li>';
                return path;
            }
            else if (toc.children && path.length === 0) {
                toc.children.forEach(function (item) {
                    path = this.findTargetNamespace(item, namespace, path);
                    return path;
                }, this);
                if (path.length) {
                    var href = '';
                    if (toc.hasOwnProperty("homepage")) {
                        href = toc.homepage;
                    }
                    path = '<li><a href="' + href + '">' + toc.toc_title + '</a></li>' + path;
                    return path;
                }
            }
            return path;
        }
    };

    return docs;
});






