define(function() {
    "use strict";

    /**
     * Load the specified JSON file from the internet asynchronously.
     * @param {string} fileUrl - The internet address of a Json file
     * @returns {JQueryPromise} The async operation which representing the retrieving process of the specified Json file
     */
    function loadJsonFileAsync(fileUrl) {
        return $.ajax({
            url: fileUrl,
            dataType: "json",
            global: false
        });
    }

    return {
        loadJsonFileAsync: loadJsonFileAsync
    };
});



