    //        ==>
    //        Current entry URL: "/vs/csharp/".
    //
    //     2. Find the current selected breadcrumb bar entry in the breadcrumb description json file
    //        Current entry URL: "/vs/csharp/"
    //        ==>
    //        Current entry:     { toc_title: "C#", href: "/vs/csharp/", children: null }
    //
    //     3. Generate the breadcrumb bar list by concatenating the ancestors of the selected entry
    //        Current entry:       { toc_title: "C#", href: "/vs/csharp/", children: null }
    //        ==>
    //        Breadcrumb bar list: [{ toc_title: "Documentation" }, { toc_title: "Visual Studio" }, {toc_title: "C#"}]
